<script>
import "aos/dist/aos.css";
import AOS from "aos";

AOS.init();

import Arrow from "./_Arrow";
export default {
  data() {
    return {
      loading: true,
      one: false,
      two: false,
      three: false,
      four: false
    };
  },
  components: {
    Arrow
  },
  methods: {
    updateLoad() {
      this.loading = false;
    },
    setOne() {
      this.one = true;
    },
    setTwo() {
      this.two = true;
    },
    setThree() {
      this.three = true;
    },
    setFour() {
      this.four = true;
    },
    test() {
      if (this.$store.state.toVenues == 1) {
        this.$scrollTo(venues, 1, { offset: -50 });
        this.$store.state.toVenues = 0;
      }
    }
  },
  mounted() {
    AOS.refreshHard();
    AOS.refresh();
    setTimeout(() => {
      this.setOne();
    }, 1000);
    setTimeout(() => {
      this.setTwo();
    }, 1500);
    setTimeout(() => {
      this.setThree();
    }, 2000);
    setTimeout(() => {
      this.setFour();
    }, 3500);
    this.test();
  }
};
</script>

